@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {

  font-family: 'Roboto', sans-serif;
  
  background: url(images/bg.png);
  color: #D1D5DE;
}

.img {
  width: 50%;
  max-width: 260px;
  margin: 2%;
  align-items: center;
  border-radius: 2%;
}


.App {
  display: flex;
	justify-content: center;
	align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  height: 100%;
  width: 100%;
}


.header {
  
  margin: 1%;
  width: 100%;
  text-align: center;

}
table {
  margin-top: 10%;
  
  text-align: center;
}
.container {
  margin-top: 10%;
  width: 100%;
  text-align: center;
}



h2 {
  margin-top: 2rem;
  font-weight: 400;
  font-size: 2rem;
  font-family: 'Ubuntu', sans-serif;
}

h2 .eth {
  font-weight: 300;
  text-transform: uppercase;
  font-size: 2rem;
}

h3 {
  margin-bottom: 1rem;
  font-family: 'Ubuntu', sans-serif;
}

h4 {
  margin-bottom: 1rem;
  
  text-transform: uppercase;
  font-family: 'Ubuntu', sans-serif;
}




button {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  height: 2rem;
  border: 0;
  padding-left: 1rem;
  padding-right: 1rem;
  cursor: pointer;
  background: #D1D5DE;
  color: #393E45;
  font-family: 'Ubuntu', sans-serif;
  box-shadow: 0 0 20px #D1D5DE;
}

.error, .success {
  padding: 1rem;
  margin-top: 1rem;
  border-radius: 5px;
}

.error {
  background: #ee3312;
  color: #D1D5DE;
}

.success {
  background: #53814D; 
  color: #D1D5DE;
}

table
{
    font-family: 'Ubuntu', sans-serif;
    font-style: bold;
    border-collapse: collapse; 
    width: fit-content;
    font-weight: 300;
 
	
  
}
